
import { Options, Vue } from 'vue-class-component';

import config from '@config';
import Profile from './service/profile';
import Wiki from './service/wiki';

import Status from './components/Status.vue';
import WikiList from './components/WikiList.vue';

@Options({
    components: {
        Status,
        WikiList,
    },
})
export default class App extends Vue {

    profile: Profile = config.profile;
    wikis: Array<Wiki> = [];
    loading: boolean = true;

    created() {
        this.query();
    }

    private async query() {
        this.wikis.length = 0;
        const quries: Array<Promise<void>> = [];
        for (const json of config.wikis) {
            const wiki = Wiki.parse(json);
            const query = wiki.query().then(succeed => {
                if (!succeed) return;
                let start = 0;
                let end = this.wikis.length;
                while (start < end) {
                    const mid = Math.floor((start + end) / 2);
                    if (this.wikis[mid].edits > wiki.edits) {
                        start = mid + 1;
                    } else {
                        end = mid;
                    }
                }
                this.wikis.splice(start, 0, wiki);
            });
            quries.push(query);
        }
        await Promise.allSettled(quries);
        this.loading = false;
    }
}
